import React, { Component, lazy, Suspense } from "react";
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import { withRouter } from 'react-router';
import HeaderBar from './components/HeaderBar';
import NotFound from './components/NotFound';
import './App.css';
import Login from "./login/Login";
import Register from "./login/Register";
import Zone from "./hub/Zone";
import Rpio from "./rpio/RpioBoard";
//import ErrorBoundary from "./components/ErrorBoundary";

const Logout = withRouter(
  lazy(() => import(/* webpackChunkName: "villains" */ './login/Logout'))
);

const Hub = withRouter(
  lazy(() => import(/* webpackChunkName: "villains" */ './hub/Hub'))
);

const Zones = withRouter(
  lazy(() => import(/* webpackChunkName: "villains" */ './hub/Zones'))
);

const Settings = withRouter(
  lazy(() => import(/* webpackChunkName: "villains" */ './hub/Settings'))
);

const base = process.env.PUBLIC_URL;

class App extends Component {
  render() {
    return (
      <BrowserRouter>
      <div className="allwidth">
          <HeaderBar />
            <Suspense fallback={<div>Laddar...</div>}>
              <Switch>
                <Route exact path={base + "/"} component={Hub} />
                <Route exact path={base + "/zones"} component={Zones} />
                <Route path={base + "/zone/:id"} component={Zone} />
                <Route path={base + "/settings"} component={Settings} />
                <Route path={base + "/rpio"} component={Rpio} />
                <Route path={base + "/login"} component={Login} />
                <Route path={base + "/register"} component={Register} />
                <Route path={base + "/logout"} component={Logout} />

                <Route exact path="**" component={NotFound} />
              </Switch>
            </Suspense>
      </div>
      </BrowserRouter>
    );
  }
}

export default App;
